/***********************************************************************/

dphelper.array = {

  /****************************************************/
  // FIND

  find: (id, array) => {

    if (!Array.isArray(array)) return

    for (const node of array) {
      if (node.id === id) return node

      if (node.children) {
        const finalNode = dphelper.array.find(id, node.children)
        if (finalNode) return finalNode
      }
    }
    return false
  },

  /****************************************************/
  // UNIQUE

  unique: (array) => {

    if (!Array.isArray(array)) return

    const unique = [...new Set(array)]
    return unique
  },

  /****************************************************/
  // DELETE

  delete: (array, id) => {

    if (!Array.isArray(array)) return

    array.some(function iter(o, i, a) {

      if (o.Id === id) {
        a.splice(i, 1)
        return true
      }

      for (const key of Object.keys(o)) {
        const value = o[key]
        if (value.length && typeof value === 'object') return value && value.map(iter)
      }

      // OLD SCHOOL WAY
      //var key = Object.keys(o);
      // for (var p=0; p < key.length; ++p){
      //  if( o[key[p]]?.length && typeof o[key[p]] === 'object') return o[key[p]] && o[key[p]].some(iter);
      // }

    })

  },

  /****************************************************/
  // MERGE

  merge: (arrayA, arrayB) => {

    for (const key of Object.keys(arrayA)) {
      if (arrayA[key] instanceof Object && arrayB[key]) Object.assign(arrayA[key], dphelper.mergeArrays(arrayB[key], arrayA[key]))
    }

    Object.assign(arrayB || {}, arrayA)
    return arrayB
  },

  /****************************************************/

  mergeByKey: (arrayA, arrayB, key) => {
    arrayA.map((item, i) => Object.values(arrayB).map(o => item[key] === o[key] ? Object.assign(arrayA[i], o) : arrayA[i]))
    return arrayA
  },

  /****************************************************/
  // ASCENDING

  asc: array => {
    if (!Array.isArray(array)) return
    array.sort((a, b) => a - b)
    return array
  },

  /****************************************************/
  // ASCENDING

  desc: array => {
    if (!Array.isArray(array)) return
    array.sort(function (a, b) { return b - a })
    return array
  },

  /****************************************************/
  // FIND DUPLICATES

  duplicates: array => {
    if (!Array.isArray(array)) return
    const
      toFindDuplicates = arr => arr.filter((item, index) => arr.indexOf(item) !== index),
      duplicateElements = toFindDuplicates(array)
    return duplicateElements
  },

  /****************************************************/
  // ONLY EVEN

  even: array => {

    if (!Array.isArray(array)) return

    const
      cur = [],
      arrayCache = array.length

    for (let i = 0; i < arrayCache; i++) {
      if (array[i] % 2 === 0) cur.push(array[i])
    } return cur
  },

  /****************************************************/
  // ONLY ODD

  odd: array => {

    if (!Array.isArray(array)) return

    const
      cur = [],
      arrayCache = array.length

    for (let i = 0; i < arrayCache; i++) {
      if (array[i] % 2) cur.push(array[i])
    } return cur
  },

  /****************************************************/
  // ARRAY TO OBJECT

  toObj: array => {
    return Object.assign({}, array)
  },

  /****************************************************/
  // SUMM OF A COLUMN IN THE ARRAY

  sumColumn: (array = [[]], column = 0) => {

    if (!Array.isArray(array)) return "provide an multidimensiona array"
    else if (typeof column === 'undefined') return "provide a column"
    else {
      var acc = 0, final
      final = array.map(d => { acc += d[column]; return acc })
      return final[final.length - 1]
    }

  },

  /****************************************************/
  // GET RANDOM POSITIONS

  shuffle: (array) => {
    if (!Array.isArray(array)) return
    for (let i = array.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [array[i], array[j]] = [array[j], array[i]]
    }
    return array
  },

  /****************************************************/
  // GENERATE RANDOM ARRAY OF NUMBERS

  generate: num => {
    if (isNaN(num)) return
    dphelper.array.shuffle(num)
  },

  /****************************************************/
  // GENERATE RANDOM ARRAY OF NUMBERS

  testArrayInt(array) {

    if (!Array.isArray(array)) return

    let n, r

    for (var i = 1; i < array.length; ++i) {
      n = (array.includes(i))

      if (n === false && n !== undefined) {
        r = i
      }

    }

    return r === ''
      ? array.length + 1
      : r

    /*
      // examples:
      console.log( dphelper.array.testArrayInt([1,2,3,4,6,7]) );     // return 5
      console.log( dphelper.array.testArrayInt([1,2,3]) );           // return 4
      console.log( dphelper.array.testArrayInt([-1,-2]) );           // return 1
    */

  },

  rand32: (total) => {

    const
      t0 = performance.now(),
      array = []

    array['nums'] = []
    array['time'] = []

    for (var i = 0; i < total; ++i) {
      array['nums'].push(crypto.getRandomValues(new Uint32Array(1))[0])
    }

    const t1 = performance.now()
    array['time'].push(t1 - t0)

    return array

  }




}

