dphelper.date = {

  months: (lang = 'en') => {
    const
      en = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
      it = ["Gen", "Feb", "Mar", "Apr", "Mag", "Giu", "Lug", "Ago", "Set", "Ott", "Nov", "Dic"]
    return lang
  },

  toIso: (value, int = 'en') => {
    if (!value) value = ''
    const date = new Date(value)
    if (String(date) == 'Invalid Date') return null //date = new Date()
    const dateTimeFormat = new Intl.DateTimeFormat(int, {
      year: 'numeric', month: 'long', day: 'numeric',
    })
    return dateTimeFormat.format(date)
  },

  toMMDDYYYY: (value) => {
    if (!value) value = ''
    const date = new Date(value)
    return ('0' + (date.getMonth() + 1)).slice(-2) + "" + ('0' + date.getDate()).slice(-2) + "" + date.getFullYear()
  },

  toYYYYMMDD: (value) => {
    if (!value) return
    return value.toISOString().split('T')[0].replaceAll('-', '')
  },

  toHuman: (value) => {
    const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }
    return value.toLocaleDateString(undefined, options)
  },

  convert: (value, format) => {

    if (!value) return null
    if (!format) format = dphelper.date.months()

    const
      month = value.substring(0, 2).replace('0', ''),
      day = value.substring(2, 4),
      year = value.substring(value.length - 4)

    return day + ' ' + format[Number(month) - 1] + ' ' + year
  },

  iso2Epoch: (value) => {
    if (!value) value = ''
    const
      date = new Date(value),
      milliseconds = date.getTime()
    return milliseconds
  },

  localIsoTime: (value) => {
    if (!value) value = ''
    const
      tzoffset = (new Date(value)).getTimezoneOffset() * 60000,
      localISOTime = (new Date(Date.now() - tzoffset)).toISOString().slice(0, -1)
    return localISOTime
  },

  utc: () => {
    const date = new Date()
    return date.toUTCString()
  },

  parse: (value, separator = '/') => {
    if (!value) return null
    const
      month = value.substring(0, 2),
      day = value.substring(2, 4),
      year = value.substring(4, 8)

    return month + separator + day + separator + year
  },

  year: () => {
    return /\d{4}/.exec(Date())[0]
  },

  addDays: (date, days) => {
    const copy = new Date(Number(date))
    copy.setDate(date.getDate() + days)
    return copy
  },

  dateTimeToString: (dateObject) => {

    try {
      return `${dateObject.getFullYear()}${dateObject.getMonth().toString().length > 1
        ? dateObject.getMonth() + 1
        : `0${dateObject.getMonth() + 1}`}
        ${dateObject.getDate().toString().length > 1
          ? dateObject.getDate()
          : `0${dateObject.getDate()}`}_${dateObject.getHours()}${dateObject.getMinutes()}${dateObject.getSeconds()}`

    } catch (err) {
      // todo: log errors
      return dateObject
    }
  },

  isoToHuman: (value, symbol = "@") => {

    if (!value) return null

    const
      format = dphelper.date.months(),
      date = new Date(value),
      year = date.getFullYear(),
      t = value.split("T")[1].split(":").slice(0, 2).join(':')

    let
      month = (date.getMonth() + 1).toString(),
      dt = date.getDate().toString()

    if (dt.length < 2) dt = '0' + dt
    if (month.length < 2) month = '0' + month

    return (dt + ' ' + format[Number(month) - 1] + ' ' + year + " " + symbol + " " + t)
  },

  fullDate: (custom = null) => {
    // 01-01-1970 00:00:00

    const
      inputDate = new Date(),
      day = Number(inputDate.getDate()),
      month = Number(inputDate.getMonth() + 1),
      year = Number(inputDate.getFullYear()),
      hours = Number(inputDate.getHours()),
      minutes = Number(inputDate.getMinutes()),
      seconds = Number(inputDate.getSeconds()),

      fulldate =
        (day < 10 ? '0' + day : day) + "-" +
        (month < 10 ? '0' + month : month) + "-" +
        year + " " +
        (hours < 10 ? '0' + hours : hours) + ":" +
        (minutes < 10 ? '0' + minutes : minutes) + ":" +
        seconds

    return fulldate
  },

  epoch: () => { return new Date().getTime() },

  diffInDays: function (d1, d2) {
    var t2 = d2.getTime()
    var t1 = d1.getTime()
    return Math.floor((t2 - t1) / (24 * 3600 * 1000))
  },

  diffInWeeks: function (d1, d2) {
    var t2 = d2.getTime()
    var t1 = d1.getTime()
    return Number(parseInt(String((t2 - t1) / (24 * 3600 * 1000 * 7))))
  },

  diffInMonths: function (d1, d2) {
    var d1Y = d1.getFullYear()
    var d2Y = d2.getFullYear()
    var d1M = d1.getMonth()
    var d2M = d2.getMonth()

    return (d2M + 12 * d2Y) - (d1M + 12 * d1Y)
  },

  diffInYears: function (d1, d2) {
    return d2.getFullYear() - d1.getFullYear()
  }

}

