/***********************************************************************/

const description = {
  "name": "element",
  "active": true,
  "subCommand": [
    {
      "name": "fitScale",
      "version": "0.0.1",
      "example": "dphelper.element.fitScale([el = 'root', scale = 1, fit = false])",
      "author": "Dario Passariello",
      "creationDate": "20230527",
      "lastMod": "20230527",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }
  ]
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.element = {

  fitScale: (el, scale = 1, fit = false) => {

    addEventListener("resize", e => {
      dphelper.element.scaleBasedOnWindow(el, scale, fit)
    })

    return

  },

  scaleBasedOnWindow: (elm, scale, fit) => {

    if (!elm) return

    const
      elem = $(elm),
      width = elem.width() || 1,
      height = elem.height() || 1

    !fit
      ? elem.css('transform', 'scale(' + scale / Math.min(width / window.innerWidth, height / window.innerHeight) + ')')
      : elem.css('transform', 'scale(' + scale / Math.max(width / window.innerWidth, height / window.innerHeight) + ')')

  }

}

////////////////////////////////////////////////////////////////////////////////////

dphelper.setProps(dphelper, description)

export { }

