
///////////////////////////////////////////////////////////////////////////////
// PROPERTY SETTER

dphelper.setProps = (root, description) => {

  Object.defineProperty(root, description.name, {
    writable: true,
    configurable: false
  })

  description.subCommand.map(d => {
    if (dphelper[d.name]) return
    Object.defineProperty(root, d.name, {
      writable: true,
      configurable: false
    })
  })

}
